export default () => {
  var html = document.documentElement, 
    //viewport
    vp = html.querySelector('meta[name="viewport"'),
    dpr = 1, rem = 100;
  //vptrue
  if(vp){
    //
    var scale = vp.getAttribute("content").match(/initial\-scale=([\d\.]+)/);
    //dpr
    dpr = parseInt(1/ scale[1])
  }else{
    // 1. dpr   devicePixelRatio
    dpr = window.devicePixelRatio
    // 2. 
    var scale = 1/dpr
    // 3. viewport
    vp = document.createElement('meta')
    vp.setAttribute('name','viewport')//
    vp.setAttribute('content','"width=device-width,user-scalable=no,initial-scale='+scale)
    // 4. head
    html.firstElementChild.appendChild(vp)
  }
  var html = document.documentElement
  var w = html.clientWidth || document.body.clientWidth;
  /* 
        HTML100px
        750px(iPhone 6/7/8)
        dpr >1 
      */
  html.style.fontSize = w / (dpr > 1 ? 7.5 : 3.75) +"px"

  document.addEventListener('DOMContentLoaded',function name(params) {
    document.body.style.fontSize = 12*dpr+'px'
  })
}



